
<template>
  <div>
    <slot>
      <el-button type="primary" @click="showDialog" size="mini">选择品牌</el-button>
    </slot>
    <el-dialog title="选择品牌" width="80%" :visible="visible" :before-close="close">
      <div style="padding:10px 15px;">
        <section class="cms-list-query">
          <div class="query-group">
            <label>品牌名称</label>
            <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
          </div>
          <div class="query-group">
            <cms-button size="small" :colorPicker='true' @click="query" style="margin-left: 50px;">查询</cms-button>
          </div>
        </section>
        <el-table :data="tableData" style="cursor: pointer;" ref='dataTable' row-key="id"
         @selection-change='getCheckItems' @row-click='checkRows'>
          <!-- 多选 -->
          <el-table-column width="60" type="selection" v-if="multiple" :reserve-selection="true">
          </el-table-column>
          <!-- 单选 -->
          <el-table-column width="60" align="center" v-else>
            <div slot-scope="scope">
              <el-radio :label="scope.$index" v-model="selectIndex">&nbsp;</el-radio>
            </div>
          </el-table-column>
          <el-table-column prop="id" label="id" width="60"></el-table-column>
          <el-table-column prop="name" label="品牌名称" min-width="200"></el-table-column>
          <el-table-column prop="initials" label="首字母" min-width="150"></el-table-column>
          <el-table-column label="LOGO" min-width="180" prop="logo">
            <div slot-scope="scope" class="table-img">
              <img v-if="scope.row.resourcesSpaceData" :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) " alt="mockimg">
            </div>
          </el-table-column>
        </el-table>
        <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align:right">
        <el-button type="primary" @click="determine" size="mini">确 定</el-button>
        <el-button type='info' @click="close" size="mini">取 消</el-button>
      </div>
    </el-dialog>
    <!--  -->
    <ul class="ad-brand__items">
      <draggable  v-model="selectBrands" :options="{animation:150}">
      <li v-for="item in selectBrands" :key="item.id" class="ad-brand__item">
        <img :src="$cms.filterUrl(item.resourcesSpaceData.url) " alt="">
      </li>
      </draggable>
    </ul>
    <span class="gray">
      设置多张可进行轮播展示，推荐6个
    </span>
  </div>
</template>
<script>
import { brandList } from '@/api/product/brand.js'
import list from '@/mixins/list'
import draggable from 'vuedraggable'
export default {
  mixins: [list],
  components: {
    draggable
  },
  props: ['value', 'selectData'],
  data() {
    return {
      multiple: true,
      visible: false,
      checkItems: [],
      selectBrands: [],
      queryParams: {
        page: 1,
        size: 10,
        name: '',
        queryType: 1
      }
    }
  },
  watch: {
    selectData: {
      handler(newVal, oldVal) {
        this.selectBrands = [...newVal]
      },
      immediate: true,
      deep: true
    },
    selectBrands: {
      handler(newVal, oldVal) {
        const arr = newVal.map(v => v.id)
        this.$emit('input', arr)
      },
      deep: true
    }
  },
  methods: {
    showDialog() {
      // 开启选择框
      this.visible = true
      this.getTableData(brandList, this.queryParams)
    },
    checkRows(row) {
      // 单击行选中
      if (this.multiple) {
        // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else {
        // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.selectBrands = Object.assign([], this.checkItems)
      this.visible = false
      this.$refs.dataTable.clearSelection()
    },
    close() {
      this.visible = false
      this.$refs.dataTable.clearSelection()
    }
  },
  created() {}
}
</script>

 <style lang="scss" scoped>
.ad-brand {
  &__items {
    overflow: hidden;
    width: 270px;
  }
  &__item {
    margin-right: 10px;
    margin-top: 10px;
    float: left;
    width: 80px;
    height: 40px;
    border: 1px solid #f0f0f0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    img{
      width: auto;
    height: auto;
    max-height: 100%;
    max-width: 100%;
    }
  }
}
</style>






