
<template>
  <div>
    <el-button @click="initData" size="mini" type="primary">选择商品</el-button>
    <el-dialog title='选择商品' :visible="dialogVisible" width="80%" :before-close="close">
      <div class="cms-dialog__body">
        <section class="cms-list-query">
          <div class="query-group">
            <label>商品名称</label>
            <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
          </div>
          <div class="query-group">
            <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          </div>
        </section>
        <el-table v-loading='loading' element-loading-text="正在加载..." :data="tableData" style="cursor: pointer;" 
         ref='dataTable' @selection-change="getSelect" @row-click="checkRows" row-key="id" height='350'>
          <!-- 多选 -->
          <el-table-column min-width="5%" type="selection"
          :reserve-selection="true"
          >
          </el-table-column>
          <el-table-column prop="product.name" label="商品名称" class-name='is-height'>
            <template slot-scope="scope">
              <div class="product-table">
                <div class="product-img">
                  <img :src="$cms.filterUrl(scope.row.coverImage)" :alt="scope.row.name">
                </div>
                <div class="product-desc">
                  <div class="product-name">
                    {{scope.row.name}}
                  </div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="价格"  prop="salePrice"  class-name='is-height'>
          </el-table-column>
        </el-table>
        <cms-pagination @change="getPage" :total='total' ></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="determine">确 定</el-button>
        <el-button type='info' size="mini" @click="close">取 消</el-button>
      </span>
    </el-dialog>
    <ul class="prodSearch-intro-ul clearfix" v-if="show">
       <draggable v-model="showItems" :options="{animation:150}">
          <li class="prodSearch-intro-li" v-for="(item,index) in showItems" :key="item.id">
            <div class="prodSearch-intro-box">
              <img :src="$cms.filterUrl(item.coverImage)" class="prodSearch-intro-box__img">
              <p class="prodSearch-intro-box__descript" v-if="item.name">
                  {{item.name}}
              </p>
              <p class="prodSearch-intro-box__descript" v-if="!item.name">
                  {{item.productName}}
              </p>
              <p class="prodSearch-intro-box__price error" v-if="item.salePrice">
                ￥{{$cms.float(item.salePrice)}}
              </p>
            </div>
            <div class="prodSearch-intro-box__cancel" @click="deleteCoupon(index)">
              取消
            </div>
          </li>
       </draggable>
    </ul>
  </div>
</template>

<script>
import { productPage } from '@/api/marketing/index.js'
import list from '@/mixins/list'
import draggable from 'vuedraggable'
export default {
  mixins: [list],
  components: { draggable },
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    type: {
      type: String,
      default: 'promote'
    },
    labelName: {
      type: String,
      default: 'name'
    },
    selectData: {
      type: Array,
      default: function() {
        return []
      }
    },
    show: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    selectData: {
      handler() {},
      deep: true
    },
    showItems: {
      handler(newVal, oldVal) {
        const arr = []
        newVal.forEach(v => {
          const obj = {
            productName: v.name || v.productName,
            productId: v.productId || v.id,
            productImgId: v.converImageId || v.productImgId
          }
          arr.push(obj)
        })
        this.$emit('input', arr)
      },
      deep: true
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      queryParams: {
        page: 1,
        size: 10,
        name: ''
      },
      total: 0,
      showItems: [] // 组件内显示的数组
    }
  },
  methods: {
    getSelect(selection) {
      this.checkItems = selection
    },
    getTableData(params) {
      productPage(params)
        .then(res => {
          if (res.code === 200) {
            this.total = res.data.totalElements
            this.tableData = res.data.content
            this.defaultToggleSelection()
          } else {
            this.$errorMessage(res.message)
          }
          this.loading = false
        })
        .catch(res => {
          this.loading = false
        })
    },
    query() {
      this.queryParams.page = 1
      this.getTableData(this.queryParams)
    },
    getPage(page, size) {
      this.queryParams.page = page
      this.queryParams.size = size
      this.getTableData(this.queryParams)
    },
    initData() {
      this.dialogVisible = true
      this.getTableData(this.queryParams)
    },
    checkRows(row) { // 获取行点击
      this.$refs.dataTable.toggleRowSelection(row)
    },
    determine() { // 必须点击确定才能变更数据
      this.showItems = this.showItems.concat(this.checkItems)
      this.$emit('get', this.showItems)
      this.close()
    },
    deleteCoupon(index) {
      this.showItems.splice(index, 1)
      this.checkItems.splice(index, 1)
      if (this.$refs.dataTable) {
        this.$refs.dataTable.clearSelection()
      }
      this.$emit('get', this.showItems)
    },
    close(done) {
      this.dialogVisible = false
      this.$refs.dataTable.clearSelection()
    }
  },
  created() {
    this.showItems = Object.assign(this.showItems, this.selectData)
  }
}
</script>

<style lang='scss' scoped>
/* 商品搜索 */

/* 商品搜索主体 */
.prodSearch-intro {
  position: relative;
  font-size: 14px;
  overflow: hidden;
}
.prodSearch-intro-li {
  cursor: pointer;
  float: left;
  width: 150px;
  height: 180px;
  border: 1px solid #e0e0e0;
  padding: 10px 10px 0 10px;
  margin-top: 10px;
  border-radius: 4px;
  margin-right: 15px;
  position: relative;
}
.prodSearch-intro-box {
  &__img {
    width: 80px;
    height: 80px;
    display: block;
    margin: 0px auto 5px auto;
  }
  &__price {
    text-align: center;
    font-size: 14px;
    color: #f56c6c;
    margin-bottom: 10px;
  }
  &__descript {
    text-align: center;
   font-size: 14px;
    color: #444;
    line-height: 20px;
    height: 40px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  &__cancel{
      display: none;
     height: 32px;
     background: rgba(0,0,0,.6);
     color: #fff;
     line-height: 32px;
     font-size: 14px;
     text-align: center;
     position: absolute;
     bottom: 0;
     left: 0;
     width: 100%;
  }
}
.prodSearch-intro-li:hover{
  .prodSearch-intro-box__cancel{
    display: block;
  }
}
</style>



