<template>
  <div>
    <el-select v-model="selectAds" :multiple="multiple" @change="changeSelectAds" size="mini">
      <el-option-group
      v-for="(group,index) in advertList"
      :key="index"
      :label="group.positionAttrName">
      <el-option
        v-for="item in group.advertisementsAttr"
        :key="item.id"
        :label="item.title"
        :value="item.id">
      </el-option>
    </el-option-group>
    </el-select>
   
  </div>
</template>

<script>
import { advertgrouopList } from '@/api/marketing/index.js'
export default {
  props: {
    multiple: {
      type: Boolean,
      default: true
    },
    value: [Array, Number, String]
  },
  data() {
    return {
      selectAds: [],
      advertList: []
    }
  },
  methods: {
    getAdvertsList() {
      advertgrouopList().then(res => {
        if (res.code === 200) {
          this.advertList = res.data
        }
      })
    },
    changeSelectAds(data) {
      this.$emit('input', data)
    }
  },
  created() {
    this.getAdvertsList()
  }
}
</script>

<style>

</style>



