<template>
  <el-tabs id="tabSort" type='card' v-model="currentTabs" editable @edit="changeCard">
    <el-tab-pane v-for="(item,index) in panelData" :key="item.name" :name="item.name">
      <span slot="label" class="tab-label__inner">
        <input type="text" v-model="item.title" class="tab-input" v-show="item.isEdit" 
         maxlength="20" @blur="saveTabName(item)" :id='"labelInput"+index'>
        <em v-show="!item.isEdit" @dblclick="editTabName(item,index)">{{item.title}}</em>
      </span>
      <component :is="floorPanel" :data="item"></component>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import floorPanel from './floorPanel'
import Sortable from 'sortablejs'
export default {
  props: ['selectData'],
  data() {
    return {
      floorPanel: floorPanel,
      currentTabs: 'tab1',
      panelData: [
        {
          name: 'tab1',
          title: '精选热卖',
          isEdit: false, // 可编辑状态
          layoutType: 1,
          adPostitionId: '',
          brandIds: [],
          productData: [],
          selectBrands: [],
          selectProducts: []
        }
      ],
      selectChildData: []
    }
  },
  watch: {
    selectData: {// 默认选中的数据回填传递出去,保持引用关系
      handler(newVal, oldVal) {
        const arr = []
        const arr2 = []
        newVal.forEach((v, i) => {
          const obj = {
            name: 'tab' + parseInt(i + 1),
            id: v.id,
            title: v.title,
            isEdit: false, // 可编辑状态
            layoutType: v.layoutType,
            adPostitionId: v.adPostitionId,
            brandIds: this.$cms.arrayToNumber(v.brandIds),
            productData: this.filterProductData(v.products),
            selectBrands: v.brands,
            selectProducts: v.products
          }
          const selectObj = {

          }
          arr.push(obj)
          arr2.push(selectObj)
        })
        this.selectChildData = arr2
        this.panelData = Object.assign([], arr)
      },
      deep: true,
      immediate: true
    },
    panelData: {
      handler(newVal, oldVal) {
        this.$emit('change', newVal)
      },
      deep: true
    }
  },
  methods: {
    filterProductData(data) { // 转化productData的数据格式，保证一致性
      const arr = []
      data.forEach(v => {
        const obj = {
          productName: v.product.name,
          productId: v.product.id,
          productImgId: v.showImageId,
          coverImage: v.showImageUrl,
          salePrice: v.product.salePrice
        }
        arr.push(obj)
      })
      return arr
    },
    editTabName(item, index) {
      // 双击编辑名称
      item.isEdit = true
      this.$nextTick(() => {
        const el = document.querySelector('#labelInput' + index)
        if (el) {
          el.focus()
        }
      })
    },
    saveTabName(item) {
      if (item.title === '') {
        item.title = '自定义选项'
      } else {
        item.isEdit = false
      }
    },
    changeCard(targetName, action) {
      // 新增或者删除选项卡
      if (action === 'add') {
        // 新增
        const index = parseInt(this.panelData.length + 1)
        const obj = {
          name: 'tab' + index,
          title: '自定义选项' + index,
          isEdit: true, // 可编辑状态
          layoutType: 1,
          adPostitionId: '',
          brandIds: [],
          productData: [],
          selectBrands: [],
          selectProducts: []
        }
        this.panelData.push(obj)
        this.currentTabs = obj.name
        this.$nextTick(() => {
          const el = document.querySelector('#labelInput' + (index - 1))
          if (el) {
            el.focus()
          }
        })
      } else { // 删除
        const len = this.panelData.length
        const index = this.panelData.findIndex(v => v.name === targetName)
        if (this.panelData[index].isEdit) { // 判断是否在编辑状态
          return false
        } else {
          if (len === 1) {
            this.$errorMessage('最少保留一项')
          } else {
            this.panelData.splice(index, 1)
            const name = this.panelData[0].name
            this.currentTabs = name
          }
        }
      }
    },
    cardSort() {
      // 选项卡拖动排序
      const el = document.querySelectorAll('#tabSort .el-tabs__nav')[0]
      Sortable.create(el, {
        animation: 150,
        onEnd: evt => {
          const oldData = this.panelData.splice(evt.oldIndex, 1)[0]
          this.panelData.splice(evt.newIndex, 0, oldData)
        }
      })
    }
  },
  mounted() {
    this.cardSort()
  }
}
</script>

 <style lang="scss" scoped>
.tab-input {
  border: none;
  font-size: 14px;
  height: 100%;
  width: 60px;
  outline: none;
}
.tab-label__inner {
  em {
    font-style: normal;
  }
}
</style>




