
<template>
  <div>
    <slot>
      <el-button type="primary" @click="showDialog" size="mini">选择分类</el-button>
    </slot>
    <el-dialog title="选择分类" width="350px" :visible="visible" :before-close="close">
      <div style="padding:10px 40px;height:500px;overflow:auto">
        <el-tree :data="data" :props="defaultProps" show-checkbox check-strictly ref="categoryTree" node-key="id"></el-tree>
      </div>
      <div slot="footer" class="dialog-footer" style="text-align:right">
        <el-button type="primary" @click="determine" size="mini">确 定</el-button>
        <el-button type='info' @click="close" size="mini">取 消</el-button>
      </div>
    </el-dialog>
    <span class="gray">窄体布局推荐最多设置10个，宽体布局推荐最多设置19个，排列在前优先置顶显示,拖动可调整排序</span>
    <div class="cate-tags">
       <draggable  v-model="checkItems" :options="{animation:100}">
      <el-tag v-for="(tag,index) in checkItems" :key="tag.id" closable disable-transitions class="cate-tag"  @close="handleClose(index)">
        <span v-show="!tag.isEdit"  @dblclick="changeEditName(index)"> {{tag.cateName}}</span>
        <input type="text" v-show="tag.isEdit" v-model="tag.cateName" class="cate-input" @blur="saveCateName(tag)"
        :id="'tagInput'+index"
        >  
      </el-tag>
       </draggable>
    </div>

  </div>
</template>
<script>
import { productCategoryTree } from '@/api/product/category.js'
import list from '@/mixins/list'
import draggable from 'vuedraggable'
export default {
  mixins: [list],
  components: { draggable },
  props: ['value', 'selectData'],
  data() {
    return {
      multiple: true,
      visible: false,
      checkItems: [],
      selectBrands: [],
      queryParams: {
        page: 1,
        size: 10,
        name: '',
        queryType: 1
      },
      defaultProps: {
        label: 'cateName',
        children: 'childs'
      },
      data: []
    }
  },
  watch: {
    selectData: {
      handler(newVal, oldVal) {
        const arr = []
        newVal.forEach(v => {
          const obj = {
            cateName: v['cate_name'],
            id: v['cate_id']
          }
          arr.push(obj)
        })
        this.checkItems = arr
      }
    },
    checkItems: {
      handler(newVal, oldVal) {
        const arr = []
        newVal.forEach(v => {
          const obj = {
            cate_name: v.cateName,
            cate_id: v.id
          }
          arr.push(obj)
        })
        this.$emit('input', arr)
        this.$emit('change', arr)
      },
      deep: true
    }
  },
  methods: {
    getTreeInfo() {
      productCategoryTree().then(res => {
        this.data = res.data
      })
    },
    showDialog() {
      // 开启选择框
      this.visible = true
      this.$nextTick(() => {
        this.getTreeInfo()
        const arr = []
        this.checkItems.forEach(v => {
          arr.push(v.id)
        })
        this.$refs.categoryTree.setCheckedKeys(arr)
      })
    },
    determine() {
      this.checkItems = Object.assign(
        [],
        this.$refs.categoryTree.getCheckedNodes()
      )
      this.visible = false
      this.$refs.categoryTree.setCheckedKeys([])
    },
    changeEditName(index) {
      this.$set(this.checkItems[index], 'isEdit', true)
      this.$nextTick(() => {
        const el = document.querySelector('#tagInput' + index)
        el.focus()
      })
    },
    saveCateName(tag) {
      tag.isEdit = false
    },
    handleClose(index) {
      this.checkItems.splice(index, 1)
    },
    close() {
      this.visible = false
      this.$refs.categoryTree.setCheckedKeys([])
    }
  },
  created() {}
}
</script>
 <style lang="scss" scoped>
 .cate-tags{
    width: 500px;
    overflow: hidden;
 }
 .cate-tag{
   float: left;
   margin-top: 10px;
   margin-right: 10px;
   cursor: pointer;
   user-select: none;
 }
 .cate-input{
   border: none;
   outline: none;
   height: 22px;
   width: 60px;
   background: transparent;
 }
</style>






