<template>
  <div class="cms-form-layout">
    <el-form :model="data" label-width="210px">
      <el-form-item label="布局模式" prop="isEnable">
        <el-radio-group v-model="data.layoutType">
          <el-radio :label="1">宽体布局</el-radio>
          <el-radio :label="2">窄体布局</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="广告版位">
        <el-select v-model="data.adPostitionId">
          <el-option v-for="item in groupList" :key="item.id" :value="item.id" :label="item.positionName"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="选择品牌">
        <brand-select v-model="data.brandIds" :selectData="data.selectBrands"></brand-select>
      </el-form-item>
      <el-form-item label="推荐商品">
        <product-select v-model="data.productData" :selectData="data.productData" >
        </product-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import advertSelect from './advertSelect'
import brandSelect from './brandSelect'
import productSelect from './productSelect'
import { advertgrouopList } from '@/api/marketing/index.js'
export default {
  props: {
    data: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  components: {
    advertSelect,
    brandSelect,
    productSelect
  },
  data() {
    return {
      groupList: []
    }
  },
  methods: {
    getAdvertGrouopList() {
      // 获取广告版位
      advertgrouopList({ isEnable: true }).then(res => {
        if (res.code === 200) {
          this.groupList = res.data
        }
      })
    }
  },
  created() {
    this.getAdvertGrouopList()
  }
}
</script>

<style>
</style>



